import {ChangeDetectorRef, Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanHealthData} from "@generated/vsan-health-data";
import {VsanTestData} from "@generated/vsan-test-data";
import {VsanHealthPropertyProvider} from "@generated/vsan-health-property-provider";
import {ManagedObject, ManagedObjectUtils} from "@service/managed-object";
import {RefreshHandler} from "@service/global-refresh.service";
import {PermissionService} from "@generated/permission-service";
import {Privileges} from "@generated/privileges";
import {ModalBuilder} from "@util/modal-builder";
import {TaskMonitorService} from "@service/task-monitor-service";
import {Logger} from "@util/logger";
import {DateRangeData} from "@component/datetime-picker/date-range-picker.component";
import {ActivatedRoute, Params} from "@angular/router";
import {VsanHistoricalHealthService} from "@generated/vsan-historical-health-service";
import {HealthLazyDataLoadingUtil} from "./health-lazy-data-loading-util";
import {HealthCapabilityData} from "@generated/health-capability-data";
import {VsanTestInstanceDetails} from "@generated/vsan-test-instance-details";
import {ScreenReaderAnnouncer} from "@service/screen-reader-announcer.service";
import {HealthContext} from "@navigation/context/health-context";
import {VsanHealthStatus} from "@generated/vsan-health-status";
import {CsdService} from "@generated/csd-service";
import {HistoricalHealthConfigService} from "@generated/historical-health-config-service";
import {HealthRetestEventData} from "../model/health-retest-event.data";
import {VsanHealthUtil} from "@util/vsan-health-util";
import {PersistentStorageService} from "@service/browser-storage.service";
import {HealthTestMessage} from "@generated/health-test-message";
import {CausedIssueHealthTestMessage} from "@generated/caused-issue-health-test-message";
import {HealthPerspectiveData} from "./model/health-perspective-data";
import {InlineViewContextKey} from "@model/inline-view-context-key.data";

@Component({
   templateUrl: "health.html",
   styleUrls: ["health.scss"],
   selector: "vsan-health",
})
export class HealthComponent implements OnInit {
   private readonly VSPHERE_HEALTH_PARAM_NAME: string = "vsphereHealth";

   /**
    * At this percentage, backend will have determined all health groups as well as their loading statuses
    */
   private static readonly TASK_FETCH_PERCENTAGE_THRESHOLD: number = 50;

   /**
    * How often should the async health data be refreshed
    */
   private static readonly TASK_FETCH_INTERVAL_MS: number = 2000;

   selectedSubTestId: string = "";

   clusterRef: ManagedObject;

   @ScreenReaderAnnouncer()
   srOnlyMessage: string;

   isVsphereHealth: boolean;
   loading: boolean;
   retestRunning: boolean = false;
   loadDataError: string;
   propagatedError: string;
   isHistoricalHealthEnabled: boolean = false;
   onlineHealthCheckSupported: boolean = false;
   isHealthTaskSupported: boolean = false;
   vsanHealthData: VsanHealthData;

   private objectRef: ManagedObject;
   timestamp: number = undefined;
   private ceipEnabled: boolean;

   hasEditPermissions: boolean = false;
   isComputeOnlyCluster: boolean = false;

   silentCheckSupported: boolean = false;

   /**
    * User selected silence checks
    */
   selectedSilenceChecks: string[] = [];

   /**
    * Display the "Overview" page instead of specific test details.
    */
   showOverviewPage: boolean;

   private _healthPerspective: HealthPerspectiveData = new HealthPerspectiveData(false, this.timestamp);

   set healthPerspective(value: HealthPerspectiveData) {
      // the object is two-way bound between parent and child. If the same object is passed back and forth it is
      // impossible to detect a change (difference from the cached one). To address this issue always work with a clone
      // of the object (new object)
      const newValue: HealthPerspectiveData = value.clone();
      if (!newValue || newValue.equals(this._healthPerspective)) {
         // not changed. Do nothing;
         return;
      }
      this._healthPerspective = newValue;
      this.reloadFromCache();
   }

   get healthPerspective(): HealthPerspectiveData {
      return this._healthPerspective;
   }

   constructor(private route: ActivatedRoute,
         private healthProvider: VsanHealthPropertyProvider,
         private historicalService: VsanHistoricalHealthService,
         private historicalConfigService: HistoricalHealthConfigService,
         private taskSvc: TaskMonitorService,
         private changeDetector: ChangeDetectorRef,
         private permissionService: PermissionService,
         private storageService: PersistentStorageService,
         private csdService: CsdService) {
   }

   async ngOnInit(): Promise<void> {
      try {
         this.loading = true;
         const params: Params = this.route.snapshot.params;
         if (params.hasOwnProperty(this.VSPHERE_HEALTH_PARAM_NAME)) {
            this.isVsphereHealth = params[this.VSPHERE_HEALTH_PARAM_NAME];
         }
         this.parseContext();
         const hasPreselectedSubTestId: boolean = !!this.selectedSubTestId;
         this.objectRef = this.clusterRef || ManagedObject.contextObject;

         this.hasEditPermissions = await this.permissionService
               .hasPermissions(this.objectRef, [Privileges.EDIT_CLUSTER()]);
         await this.checkCapabilities();

         // following properties are only for cluster
         if (ManagedObjectUtils.isCluster(ManagedObject.uid(this.objectRef))) {
            this.isComputeOnlyCluster = await this.csdService.isComputeOnlyCluster(this.objectRef);
         }

         await this.fetch();

         if (!hasPreselectedSubTestId) {
            // Open "Overview" page by default if supported.
            this.showOverviewPage = !VsanUiUtils.isUnset(this.vsanHealthData?.overviewData);
         }
      } catch {
         this.handleError();
      }
   }

   private parseContext(): void {
      const context: HealthContext = this.getContext();
      this.selectedSubTestId = context?.selectedSubTestId;
      this.clusterRef = context?.clusterRef;
   }

   private getContext(): HealthContext {
      let context: HealthContext = VsanUiUtils.getNavigationData() as HealthContext;
      if (!context) {
         // In order to know if the view is inside vSAN overview tab,
         // UI needs to check if the persistence service holds any context
         context = this.storageService.pop(InlineViewContextKey.HEALTH_CHECK_CONTEXT_KEY) as HealthContext;
      }
      return context;
   }

   @RefreshHandler
   private async fetch(): Promise<void> {
      await this.reloadFromCache();
   }

   private async checkCapabilities(): Promise<void> {
      try {
         const capabilityData: HealthCapabilityData = await this.healthProvider.getHealthCapabilityData(
               this.objectRef, this.isVsphereHealth);
         this.silentCheckSupported = capabilityData.isSilentCheckSupported;
         if (!this.isVsphereHealth) {
            this.onlineHealthCheckSupported = capabilityData.isCloudHealthSupported;
            this.ceipEnabled = capabilityData.isCeipServiceEnabled;
            if (capabilityData.isHistoricalHealthSupported) {
               this.getHistoricalHealthEnabled();
            }
            this.isHealthTaskSupported = capabilityData.isHealthTaskSupported;
         }
      } catch {
         this.handleError();
      }
   }

   private getHistoricalHealthEnabled(): void {
      this.historicalConfigService.getConfig(this.objectRef).then(result =>
            this.isHistoricalHealthEnabled = result.isEnabled);
   }

   private async getSilenceChecks(): Promise<string[]> {
      try {
         if (!this.silentCheckSupported) {
            return;
         }
         return await this.healthProvider.getSilentChecks(this.objectRef, this.isVsphereHealth);
      } catch {
         this.handleError();
      }
   }

   async reloadFromCache(): Promise<void> {
      try {
         this.resetErrors();
         this.loading = true;

         this.selectedSilenceChecks = await this.getSilenceChecks();

         const healthData: VsanHealthData = this.healthPerspective.isHistoricalHealthPerspective
            ? await this.historicalService
                     .getHistoricalHealthSummary(this.objectRef, this.healthPerspective.from, this.healthPerspective.to)
            : await this.healthProvider
                     .getVsanHealthFromCache(this.objectRef, true, this.isVsphereHealth);

         this.populateData(healthData);

         this.informScreenReader();
      } catch {
         this.handleError();
      } finally {
         this.loading = false;
      }
   }

   async onRetest(event: HealthRetestEventData): Promise<void> {
      await this.retest(event.retestOnlineHealth);
   }

   async retest(retestOnlineHealth: boolean = false): Promise<void> {
      this.resetErrors();
      this.loading = true;
      this.retestRunning = true;

      this.selectedSilenceChecks = null;
      this.selectedSilenceChecks = await this.getSilenceChecks();

      if (!this.isHealthTaskSupported) {
         if (retestOnlineHealth) {
            // If online health should be retested, wait for it, then do the normal test
            try {
               const onlineHealthTask: ManagedObject = await this.healthProvider
                     .getCloudHealthCheckResult(this.objectRef);
               await this.taskSvc.getAwaitResult(onlineHealthTask);
            } catch (err) {
               this.handleOnlineHealthError(err);
            }
         }
         try {
            const vsanHealthData: VsanHealthData =
                  await this.healthProvider.getVsanHealth(this.objectRef, true, this.isVsphereHealth);
            this.populateData(vsanHealthData);
         } catch {
            this.handleError();
         }

         this.retestRunning = false;
         this.informScreenReader();
      } else {
         this.startAsyncHealthLoading(retestOnlineHealth);
      }
   }

   private resetErrors(): void {
      this.loadDataError = null;
      this.propagatedError = null;
   }

   private async startAsyncHealthLoading(retestOnlineHealth: boolean = false): Promise<void> {
      const taskRef: ManagedObject =
            await this.healthProvider.startClusterHealthCheckTask(this.objectRef, retestOnlineHealth);

      // all groups will be ready when the task has reached the required percentage
      await this.taskSvc.getAwaitResult(
            taskRef,
            HealthComponent.TASK_FETCH_PERCENTAGE_THRESHOLD,
            TaskMonitorService.TIME_BETWEEN_CALLS_FREQUENT_MS,
      );

      // TODO akolov: inform the screenreader for loading test groups and when a group loads, also inform it

      const fetchResult: () => Promise<void> = () =>
            this.healthProvider.getClusterHealthCheckTaskResult(this.objectRef, taskRef)
                  .then(this.populateData)
                  .catch(this.handleError);

      // start polling the health checks
      const pollInterval: number = window.setInterval(fetchResult, HealthComponent.TASK_FETCH_INTERVAL_MS);

      // when the task is done, stop the polling
      this.taskSvc.getAwaitResult(taskRef).finally(() => {
         fetchResult(); // do a final fetch in order to be sure that all tests are available
         this.retestRunning = false;
         clearInterval(pollInterval);
      });
   }

   handleError = (): void => {
      this.loadDataError = this.getMonitorHealthErrorMessage();
      this.loading = false;
   };

   private populateData = (result: VsanHealthData) => {
      this.loading = false;

      if (!result || !result.testsData) {
         // for historical health is expected to have empty or null result when there is no historical data for
         // selected period. Do not set error message
         if (!this.healthPerspective || !this.healthPerspective.isHistoricalHealthPerspective) {
            this.loadDataError = this.getMonitorHealthErrorMessage();
         }
         this.vsanHealthData = result;
         return;
      }

      // historical health has no data about last execution timestamp. Do not reset it.
      if (!this.healthPerspective.isHistoricalHealthPerspective) {
         this.timestamp = result.timestamp;
         this._healthPerspective = new HealthPerspectiveData(false, this.timestamp);
      }

      // tests that was skipped after last retest, after global refresh will be retrieved from cache with old status.
      // fix the status according to the selectedSilenceChecks, where the data is always up-to-date
      result.testsData.forEach(category => category.subtests.forEach(subtest => {
         if (!VsanUiUtils.isEmpty(this.selectedSilenceChecks) &&
               this.selectedSilenceChecks.some(silencedCheckId => silencedCheckId === subtest.silenceTestId)) {
            subtest.status = VsanHealthStatus.skipped;
         }
      }));

      this.vsanHealthData = result;

      if (this.healthPerspective.isHistoricalHealthPerspective) {
         this.setTestLazyDetails(this.vsanHealthData);
      }

      // Detect changes to get latest value of this.cards
      this.changeDetector.detectChanges();
   };

   private informScreenReader(): void {
      if (!this.selectedSubTestId) {
         this.srOnlyMessage = VsanUiUtils.getString("vsan.screenReader.message.page.loaded",
               VsanUiUtils.getString("vsan.monitor.health.title"));
         return;
      }
      const selectedTest: VsanTestData = this.getTestById(this.selectedSubTestId, this.vsanHealthData);
      if (selectedTest) {
         this.srOnlyMessage = VsanUiUtils.getString("vsan.screenReader.message.page.loaded.andItemSelected",
               VsanUiUtils.getString("vsan.monitor.health.title"), selectedTest.testName);
      }
   }

   private handleOnlineHealthError = (error: string): void => {
      this.loadDataError = VsanUiUtils.getString("vsan.monitor.health.onlinehealth.error");
      Logger.error(error);
   };

   retestClicked = () => {
      if (!this.onlineHealthCheckSupported || !this.hasOnlineHealthData(this.vsanHealthData)) {
         this.retest();
         return;
      }

      new ModalBuilder("health/retestConfirm")
            .setSize(650, 300)
            .open({ceipEnabled: this.ceipEnabled})
            .then((confirm) => {
               const retestWithOnlineHealth: boolean = confirm.includeOnlineHealth;
               this.retest(retestWithOnlineHealth);
            });
   };

   private hasOnlineHealthData = (vsanHealthData: VsanHealthData): boolean => {
      const onlineHealthData: VsanTestData =
            vsanHealthData.testsData.find(testData => this.isOnlineHealthCheck(testData));
      let hasOnlineTests: boolean = onlineHealthData && onlineHealthData.subtests.length !== 0 && this.ceipEnabled;
      if (hasOnlineTests) {
         // find the silent items that belong to the online health category
         const skippedOnlineHealthItems: VsanTestData[] = onlineHealthData.subtests
               .filter(subtest => this.selectedSilenceChecks
                     .some(check => check === subtest.silenceTestId),
               );
         // if the length two array is the same, all the online health are silent
         hasOnlineTests = skippedOnlineHealthItems.length !== onlineHealthData.subtests.length;
      }

      return hasOnlineTests;
   };

   private isOnlineHealthCheck = (testCategory: VsanTestData): boolean => {
      /**
       * We don't have an ID for each health check categories,
       * Here we can only check if 'ceip check' or 'support insight check' is in the subtests,
       * if in then we know this is online health category
       */
      return testCategory.subtests &&
            testCategory.subtests.some(subtest => subtest.testId === VsanHealthUtil.CEIP_TEST_ID
                  || subtest.testId === VsanHealthUtil.SUPPORT_INSIGHT_TEST_ID);
   };

   private getMonitorHealthErrorMessage = (): string => {
      if (this.isVsphereHealth) {
         return VsanUiUtils.getString("vsphereHealth.error");
      }

      return VsanUiUtils.getString("vsan.monitor.health.health.data.error");
   };

   private setTestLazyDetails(vsanHealthData: VsanHealthData): void {
      vsanHealthData.testsData.forEach(testData =>
            testData.subtests.forEach(subtest =>
                  HealthLazyDataLoadingUtil.initLazyLoading(subtest, this.loadTestInstances,
                        [subtest, this.objectRef, testData.testId, subtest.testId, this.healthPerspective.dateRange]),
            ),
      );
   }

   loadTestInstances = async (testData: VsanTestData, objectId: ManagedObject, groupId: string, testId: string,
         dateRange: DateRangeData): Promise<boolean> => {
      try {
         const testInstaces: VsanTestInstanceDetails[] = await this.historicalService.getHistoricalHealthForTest(
               objectId, groupId, testId, dateRange.from, dateRange.to);
         // set lazy loading callback for each instance
         testInstaces.forEach(instance => HealthLazyDataLoadingUtil.initLazyLoading(instance, this.loadTestTables,
               [instance, objectId, groupId, testId, instance.timestamp]));
         testData.instances = testInstaces;
      } catch (e) {
         return Promise.reject(e);
      }
      return Promise.resolve(true);
   };

   loadTestTables = async (testInstance: VsanTestInstanceDetails, objectId: ManagedObject, groupId: string,
         testId: string, instanceTimestamp: number): Promise<boolean> => {
      try {
         const instanceDetails: VsanTestInstanceDetails =
               await this.historicalService.getHistoricalHealthForTestInstance(objectId, groupId, testId,
                     instanceTimestamp);
         testInstance.details = instanceDetails?.details;
         testInstance.messages = this.assignCoreIssuesNames(instanceDetails?.messages);
      } catch (e) {
         return Promise.reject(e);
      }
      return Promise.resolve(true);
   };

   private assignCoreIssuesNames(healthMessages: HealthTestMessage[]): HealthTestMessage[] {
      if (!healthMessages) {
         return null;
      }
      healthMessages
         .filter(healthMessage => (healthMessage as CausedIssueHealthTestMessage).coreIssueTest)
         .forEach(healthMessage => {
            const causedIssueMessage: CausedIssueHealthTestMessage = healthMessage as CausedIssueHealthTestMessage;
            // Need to find the test name in already fetched tests.
            causedIssueMessage.coreIssueTest.testName =
                  this.getTestById(causedIssueMessage.coreIssueTest.testId, this.vsanHealthData).testName;
         });
      return healthMessages;
   }

   private getTestById(id: string, healthData: VsanHealthData): VsanTestData {
      if (!healthData || !healthData.testsData) {
         return null;
      }
      for (const test of healthData.testsData) {
         if (test.testId === id) {
            return test;
         }

         if (test.subtests) {
            for (const subTest of test.subtests) {
               if (subTest.testId === id) {
                  return subTest;
               }
            }
         }
      }
   }
}
